import React, { Component } from 'react';
import { List, NavBar, Icon } from 'antd-mobile';

import { webviewDestroy } from '../../../util';
import './PageCss.less';

const Item = List.Item;

class Entrance extends Component {
  listConfig = [
    {
      type: '',
      label: '锁定保护',
      /* eslint-disable */
      link: location.href.replace('center', 'lock'),
      /* eslint-enable */
    },
    {
      type: '',
      label: '解除锁定保护',
      /* eslint-disable */
      link: location.href.replace('center', 'unlock'),
      /* eslint-enable */
    },
    // {
    //   label: '注销帐号',
    //   link: ' ',
    // },
    // {
    //   type: '',
    //   label: '常见问题',
    //   link: '',
    // },
  ];

  render() {
    return (
      <div className="entrance-content">
        <NavBar
          className="i-navbar"
          leftContent={<Icon type="left" />}
          onLeftClick={() => {
            webviewDestroy();
          }}
        >
          火山安全中心
        </NavBar>
        <div className="i-body">
          <div className="i-image" />
          <List className="my-list">
            {this.listConfig.map(item => (
              <a href={item.link} key={item.link}>
                <Item arrow="horizontal">{item.label}</Item>
              </a>
            ))}
          </List>
        </div>
      </div>
    );
  }
}

export default Entrance;



